<template>
        <vue-perfect-scrollbar
                :title="init()"
                @ps-y-reach-end="reachEnd"
                :settings="{minScrollbarLength: 20}"
                :style="{height: height + 'px'}"
                class="scroll-area"
                v-scroll:debounce="{fn: onScroll, debounce: 100 }">
           <div :style="{height: height + 'px'}" class="privacy-scroller" v-html="paragraph.text"> </div>
        </vue-perfect-scrollbar>
</template>

<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
// import _delay from 'lodash/delay'
// import rx from 'rx-lite-dom'

export default {
  name: 'PrivacyScroller',
  components: {
    VuePerfectScrollbar
  },
  mounted () {
    this.paragraph.scrolled = false
    this.height = this.$el.clientHeight
    console.log('---- scroller mounted')
    if (!this.height || this.height === 0) {
      this.height = 85
    }

    if (!this.greaterThan600) {
      this.height = 100
    }

    /* rx.DOM
      .fromMutationObserver(this.$el, {attributes: true, _attributeFilter: ['target.scrollTop']})
      .subscribe(o => {
        try {
          console.dir(o)
        } catch (e) {}
      }) */

    this.checkScrolled()
    this.$el.scrollTop = 1
    this.inited = true
  },
  watch: {
    '$el.scrollHeight' (val) {
      console.log('scroll', val)
    }
  },
  data () {
    return {
      height: window.innerWidth > 600 ? 85 : 100,
      isEnd: false,
      inited: false,
      started: false,
      greaterThan600: window.innerWidth > 600
    }
  },
  methods: {
    init () {
      if (this.started) {
        console.log('--- scroller alredy started')
        return ''
      }
      console.log('--- scroller init.call')
      this.paragraph.scrolled = false
      if (!this.greaterThan600) {
        this.height = 100
      } else {
        this.height = 85
      }
      this.checkScrolled()
      return ''
    },
    reachEnd () {

    },
    checkScrolled () {
      if (!this.$el) {
        console.log('--- checkScrolled with $el not mounted')
        return
      }
      this.isEnd = this.$el.scrollTop + this.height + 5 >= this.$el.scrollHeight

      console.log('--- checkScrolled', this.isEnd, this.$el.scrollTop, this.height, this.$el.scrollHeight, this.paragraph.scrolled)

      if (this.isEnd) {
        this.$emit('scrolled', true)
        this.paragraph.scrolled = true
      } else {
        this.$emit('scrolled', false)
        this.paragraph.scrolled = false
      }

      return this.isEnd
    },
    onScroll () {
      this.checkScrolled()
    }
  },
  created () {
    this.paragraph.scrolled = false
    this.started = true
  },
  props: {
    paragraph: {type: Object}
  }
}
</script>

<style lang="scss">
    .privacy-scroller {
        text-align: left;
        padding: 2px 10px;
        min-height: 50px;
        font-family: arial;
        font-size: 11px;
    }

    .scroll-area {
        background: white;
        border:1px solid silver;
        position: relative;
        margin: auto;
        margin-bottom:3px !important;
        width: auto;
        min-height: 50px;
        font-family: arial;
        font-size: 11px;
    }
</style>



